.todo__input {
  display: block;
  width: 100%;
  max-width: 60rem;
  margin: 0 auto;
  text-align: left;
  background: rgba(103, 146, 103, 0.4);
  border: 0.4rem solid var(--font-color);
  padding: 1rem;
  font-size: 2.4rem;
  color: var(--font-color);
  font-family: "Roboto", sans-serif;
  box-shadow: -1px -1px 13px -5px rgba(0, 0, 0, 0.64);
  font-weight: 100;
}

.todo__title {
      font-family: "Ubuntu", sans-serif;
  font-size: 8rem;
  flex: 1 1 100%;
  text-align: center;
  color: #222;
  margin-bottom: 1.4rem;

}
